import { TokenAmount, Pair, Currency, JSBI } from '@oolongswap/oolongswap-sdk'
import { useMemo } from 'react'
import { abi as IOolongSwapPairABI } from '@oolongswap/oolongswap-core/build/contracts/IOolongSwapPair.json'
import { Interface } from '@ethersproject/abi'
import { useActiveWeb3React } from '../hooks'

import { useMultipleContractSingleData, useSingleContractMultipleData } from '../state/multicall/hooks'
import { wrappedCurrency } from '../utils/wrappedCurrency'
import { useFactoryContract } from 'hooks/useContract'
import { NEVER_RELOAD } from '../state/multicall/hooks'
const PAIR_INTERFACE = new Interface(IOolongSwapPairABI)

export enum PairState {
  LOADING,
  NOT_EXISTS,
  EXISTS,
  INVALID
}

export function usePairs(currencies: [Currency | undefined, Currency | undefined][]): [PairState, Pair | null][] {
  const { chainId } = useActiveWeb3React()

  const tokens = useMemo(
    () =>
      currencies.map(([currencyA, currencyB]) => [
        wrappedCurrency(currencyA, chainId),
        wrappedCurrency(currencyB, chainId)
      ]),
    [chainId, currencies]
  )

  const pairAddresses = useMemo(
    () =>
      tokens.map(([tokenA, tokenB]) => {
        return tokenA && tokenB && !tokenA.equals(tokenB) ? Pair.getAddress(tokenA, tokenB) : undefined
      }),
    [tokens]
  )
  const reserveResults = useMultipleContractSingleData(pairAddresses, PAIR_INTERFACE, 'getReserves')
  const pairFeeResults = useSingleContractMultipleData(useFactoryContract(), 'getPairFee',  pairAddresses.map(address => [address]), NEVER_RELOAD)

  return useMemo(() => {
    return pairAddresses.map((pairAddress, i) => {
      const { result: reserveResult, loading: loadingReserves } = reserveResults[i]
      const { result: pairFeeResult, loading: loadingPairFee } = pairFeeResults[i]
      const tokenA = tokens[i][0]
      const tokenB = tokens[i][1]

      if (loadingReserves || loadingPairFee) return [PairState.LOADING, null]
      if (!tokenA || !tokenB || tokenA.equals(tokenB)) return [PairState.INVALID, null]
      if (!reserveResult || !pairFeeResult) return [PairState.NOT_EXISTS, null]

      const { reserve0, reserve1 } = reserveResult
      const [token0, token1] = tokenA.sortsBefore(tokenB) ? [tokenA, tokenB] : [tokenB, tokenA]
      return [
        PairState.EXISTS,
        new Pair(new TokenAmount(token0, reserve0.toString()), new TokenAmount(token1, reserve1.toString()), JSBI.BigInt(pairFeeResult[0].toString()))
      ]
    })
  }, [pairAddresses, reserveResults, pairFeeResults, tokens])
}

export function usePair(tokenA?: Currency, tokenB?: Currency): [PairState, Pair | null] {
  return usePairs([[tokenA, tokenB]])[0]
}
